import VueRouter from 'vue-router'

//引入组件
import StartIndex from "../components/StartIndex/StartIndex.vue"
import RingImage from "../components/Ring_image/RingImage.vue"
import TimeImage from "../components/Time_image/TimeImage.vue"
import EndHtml from "../components/EndHtml/EndHtml.vue"
export default new VueRouter({
    routes:[
        {
            path:"/",
            component:StartIndex,
            name:'StartIndex'
        },
        {
            path:"/RingImage",
            component:RingImage,
            name:"RingImage"
        },
        {
            path:"/TimeImage",
            component:TimeImage,
            name:"TimeImage"
        },
        {
            path:"/EndHtml",
            component:EndHtml,
            name:"EndHtml"
        }
    ]
})