<template>
    <div class="room">
        <iframe src="//player.bilibili.com/player.html?aid=988512074&bvid=BV1o44y1S7g7&cid=916719242&page=1" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true"> </iframe>        <div class="bod" @click="roll" id="bod"></div>
        <div id="roll_word">
            <img src="./img/roll_wordimage.png" alt="" id="image">
        </div>
    </div>
</template>

<script>
    export default {
        name:'EndHtml',
        data(){
            return{
                windowHeight:0
            }
        },
        methods:{
            roll(){
                var bod = document.getElementById("bod")
                bod.style.pointerEvents = "none"
                var image = document.getElementById("image")
                image.style.transform = "translateY(-4000px)"
                image.style.transition = "transform 186s linear"
            }   
        },
        mounted(){
            
            
        }
    }
</script>

<style scoped>
    .room{
        width: 100vw;
        height: 100vh;
        background-color: black;
        overflow: hidden;
    }
    .bod{
        position:absolute;
        top: 50%;
        transform: translate(0,-50%);
        width: 50%;
        height: 50%;
        opacity: 0;
    }
    .room iframe{

        position:absolute;
        top: 50%;
        left: 6%;
        transform: translate(0,-50%);
        width: 50%;
        height: 50%;
        background-color: white;
    }
    .room .frame{
        width: 50%;
        height: auto;
    }
    .room #roll_word{
        overflow: hidden;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 9%;
        width: 400px;
        height: 70vh;
    }
    .room #roll_word img{
        transform: translateY(110vh);
        width: 100%;
        height: auto;
    }
    #iframeid{
        pointer-events: none;
    }
</style>