            },
            init(delayTime){
                let  radiu = this.radius
                var x = 360 / document.querySelectorAll('.img').length
                document.querySelectorAll('.img').forEach(function(el,index){
                    let angle = (index * x + 80 ) + "deg";
                    el.style.transform = "rotateY(" + angle + ") translateX("+radiu+"px)" 
                    el.style.transition = "transform 1s linear"
                    el.style.transitionDelay=delayTime||(document.querySelectorAll('.img').length-index)/10+"s"
                })
                this.screen_flag = false
            },
            // 鼠标移动事件
            mousemove(e){
                let fg = this.flag;
                if(fg == 1){
                    e = e|| window.event;
                    this.endX = e.clientX;
                    this.endY = e.clientY;
                    this.desX = this.endX - this.startX;
                    this.desY = this.endY - this.startY;
                    this.tX += this.desX * 0.1;
                    this.tY += this.desY * 0.1;
                    this.changeRotate();
                    this.startX = this.endX;
                    this.startY = this.endY;
