    #frame{
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }
    #frame .black_screen{
        width: 100vw;
        height: 100vh;
        background-color: #000;
        position: absolute;
        top: 0%;
        animation: fate 3s linear;
        animation-fill-mode: forwards;
    }
    @keyframes fate {
        0%{
            opacity:100%;
        }
        100%{
            opacity:0%;
        }
    }
    .room{
        width: 100px;
        height: 200px;
        margin:35vh auto;
        transform-style:preserve-3d ;
        perspective: 8000px;
        transform: rotateX(330deg) rotateY(2deg);
    }

    .room  img{
