<script>
    export default {
        data(){
            return{
                scrollY:0,
                windowHight:0,
                windowWidth:0,
                imageHeight:0,
                scrollHeight:2049
            }
        },
        methods:{
            toend(){
                this.$router.push("/EndHtml")
            },
            mousewheel(e){
              this.scrollY += e.wheelDelta/20;
              if(this.scrollY <= -this.scrollHeight) this.scrollY = -this.scrollHeight;
              if(this.scrollY >= 0) this.scrollY = 0;
              document.getElementById("timeline").style.transform = "translateY("+this.scrollY+"px)" 
              document.getElementById("timeline1").style.transform = "translateY("+this.scrollY+"px)" 
            },

            // 图片轮播
            change(x){
                let t = x-1;
                let height = -window.innerHeight * t;
                document.getElementById("midimage").style.transform = "translateY("+height+"px)"
            }
        },
        mounted(){
            document.getElementById("rom").style.overflow = "hidden"
            this.windowHight = window.innerHeight
            // this.scrollHeight = document.getElementById("timeimage").offsetHeight - this.windowHight 
        }
    }
</script>

